import React from 'react';

import profileImage from '../../image/Profile.png';
import github from '../../image/git.svg'
import mail from '../../image/mail.png'

import './index.scss';

const About = (props) => {

    return (
        <div className='contentsbox' id='about'>
            <h2><span>Ab</span>out</h2>
            <div className='about-content'>
                <div className='profile'>
                    <img className='profile-image' src={profileImage} alt='' width='106.2' height='141.6' />
                    <h3>김윤지 ( Yunji Kim )</h3>
                    <p><img width='20' alt='' height='20' src={mail} />normaltic@gmail.com</p>
                    <p><img width='20' alt='' height='20' src={github} />github.com/Normaltic</p>
                </div>
                <div className='introduce'>
                    <p>대학교를 다니다가 교과정 외 경험을 하고싶어 이것저것 해보다 React를 접하게 되었고, 이후 진행한 프로젝트들에서 React로 클라이언트를 개발하며, 개발에 필요한 기술들을 공부하며 프론트엔드 개발에 대한..</p>
                    <p>Webpack과 Babel을 사용해 기본적인 개발 환경을 구축할 수 있고, React와 Redux를 활용하여 프론트엔드 개발을 진행할 수 있습니다. 최근에는 Typescript, SCSS, Redux-form 등을 경험해 보았습니다.</p>
                    <p>저는 누가 보더라도 Awesome! 한 코드를 작성하는, 스스로를 ‘개발자’ 라고 소개할 수 있는 그런 개발자가 되고 싶습니다.</p>
                </div>
            </div>
        </div>
    );
}

export default About;