import React, { useState, useEffect } from 'react';

import TitleImage from '../../image/Title.PNG';

import './index.scss';

const Title = (props) => {
    
    const [ text, setText ] = useState('');

    useEffect( () => { 
        const titleText = '웹 개발자 김윤지의 포트폴리오 입니다.';
        const timeoutEvent = (index, text, setText) => () => {
            let nextText = text + titleText[index++];
            setText(nextText);
            if( titleText.length > index ) setTimeout(timeoutEvent(index, nextText, setText), 200);
        }
        setTimeout(timeoutEvent(0, text, setText), 200)
    }, [] );    

    return (
        <div className='title'>
            <div className='filter' />
            <div className='title-text'>
                <h2>{text}</h2>
            </div>
        </div>
    )
}

export default Title;