import React, { useState } from 'react';

const DevExp = (props) => {

    const [ is_open, setOpen ] = useState(false);

    return (
        <div className='contentsbox-gray' id='devexp'>
            <h2><span>Exp</span>erience</h2>
            <div className='devexp-timeline'>
                <div className='timeline-linebase' />
                <div className='timeline-lineend' />
                <div className='timeline-office' onClick={ () => setOpen(!is_open) }>
                    <div className='timeline-dot' />
                    <h3>Dongseo Univ.</h3>
                    <h4>학사, 2012.03 ~ 2019.02</h4>
                    <h5>여기를 누르시면</h5>
                    <h5>대학 활동 타임라인이 열립니다.</h5>
                </div>
                <div className={`timeline-itemlist-${ is_open ? 'open' : 'close' }`}>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>18.03 ~ 18.06</div>
                        <h3>강의용 웹 그림판</h3>
                        <h4>React, Android App, Node.js</h4>
                        <p> 웹 그림판을 강의에 사용할 때 사용자들(강사,학생)간의 상호작용을 위해, 강사가 하는 작업의 실시간 공유, 학생의 개인 필기 제출 등의 기능을 추가한 웹 페이지를 개발</p>
                        <div className='divide' />
                        <h3>LED 트리 잔반통</h3>
                        <h4>Arduino</h4> 
                        <p>잔반의 양이 증가할수록 트리의 LED를 소등하는 잔반통을 제작</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>18.01.22 ~ 18.01.23</div>
                        <h3>유통기한 관리 어플리케이션</h3>
                        <h4>Android App, Node.js</h4>
                        <p> 입력한 제품들의 유통기한을 캘린더 뷰로 확인할 수 있는 안드로이드 앱을 개발</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>17.10 ~ 18.01</div>
                        <h3>웹 기반 공용 그림판</h3>
                        <h4>React, Node.js</h4>
                        <p> 스마트 칠판과 유사한 기능을 가지고, 접속한 사용자들의 입력이 실시간으로 공유되는 HTML5 Canvas 기반의 웹 그림판 개발</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>17.09 ~ 17.12</div>
                        <h3>일정 조율 캘린더</h3>
                        <h4>React, Android App, Node.js</h4>
                        <p> 일정 조율 캘린더를 리액트를 활용하여 반응형 웹과 하이브리드 앱으로 개발</p>
                        <div className='divide' />
                        <h3>팀 과제 대시보드 시스템 설계</h3>
                        <h4>UML</h4>
                        <p> 강의에서 진행되는 팀 과제(프로젝트)들을 대시보드 형태로 표현해주는 시스템을 설계</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>17.08.22 ~ 17.08.24</div>
                        <h3>Book Check</h3>
                        <h4>Android App</h4>
                        <p> 책의 책등을 찍으면 해당 책의 목차를 알려주는 안드로이드 앱을 개발</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>17.3 ~ 17.06</div>
                        <h3>일정 조율 캘린더</h3>
                        <h4>Android App, Node.js</h4>
                        <p> 기존 캘린더에서, 여러 사용자가 참여하는 일정을 계획할 때, 조율 및 의사소통을 할 수 있는 기능 외 몇 가지 편의 기능을 추가한 캘린더를 개발</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>17.02.09 ~ 17.02.11</div>
                        <h3>Shooting in House</h3>
                        <h4>Unity VR, Arduino</h4>
                        <p> Arduino로 만든 컨트롤러를 연동한 HTC VIVE 슈팅 게임을 개발</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>17.01.23 ~ 17.01.25</div>
                        <h3>WareHouse</h3>
                        <h4>Unity VR</h4>
                        <p> 게임 패드를 연동한 Oqulus VR 슈팅 게임을 개발</p>
                    </div>
                    <div className='timeline-item'>
                        <div className='timeline-branch' />
                        <div className='timeline-date'>16.09 ~ 16.10</div>
                        <h3>2048, 지뢰찾기, 팩맨</h3>
                        <h4>Java Swing</h4>
                        <p> Java와 알고리즘 구현 공부를 위해 게임 구현</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default DevExp;