import React, { useState, useEffect } from 'react';

import './index.scss';

// Data
const menuList = [
    { name: 'Title', targetid: 'title' },
    { name: 'About', targetid: 'about' },
    { name: 'Skills', targetid: 'skills' },
    { name: 'Education', targetid: 'education' },
    { name: 'Projects', targetid: 'projects' },
    { name: 'Etc', targetid: 'etc' },
]

// Function
const clickToScroll = (e) => {
    const targetElm = document.getElementById(e.target.getAttribute('targetid'));
    const result = targetElm ? targetElm.scrollIntoView() : null;
    return result;
}

const closeHeader = (setState) => (e) => {
    setState(false);
    window.removeEventListener('click', this);
}
const openHeader = (setState) => (e) => {
    e.stopPropagation();
    setState(true);
    window.addEventListener('click', closeHeader(setState));
}

// Component
const HeaderItem = (is_mobile) => (
    <ul className={`Header-items`}>
        { 
            menuList.map ( (item, i) => 
                <li className={`Header-item${ is_mobile ? '' : '-inline-block'}`} onClick={clickToScroll} targetid={item.targetid} key={i}>{item.name}</li> 
            )
        }
    </ul>
)

const MobileHeader = (is_open, setState) => (
    <React.Fragment>
        <div className={`button-${ is_open ? 'status-open' : 'status-close'}`} onClick={ is_open ? closeHeader(setState) : openHeader(setState) }>
            <div className='buttonBar1' />
            <div className='buttonBar2' />
            <div className='buttonBar3' />
        </div>
        { is_open ? HeaderItem(true) : null }
    </React.Fragment>
)

const Header = (props) => {

    const [ is_mobile, setMobile ] = useState(false);
    const [ is_open, setOpen ] = useState(false);
    const [ is_loading, setLoading ] = useState(false);

    useEffect( () => {
        if( window.innerWidth < 1200 ) setMobile(true);
        else setMobile(false);
        window.addEventListener('resize', () => {
            if( window.innerWidth < 1200 && !is_mobile ) setMobile(true);
            else if( window.innerWidth >= 1200 && is_mobile ) setMobile(false);
        })
        setLoading(true);
    }, [is_mobile])

    return (
        <header className={`headerBar${ !is_mobile ? '-big' :'-small'}`}>
            { is_loading ? ( !is_mobile ? HeaderItem(is_mobile) : MobileHeader(is_open, setOpen) ) : null }
        </header>
    );
}

export default Header;