import React from 'react';

import './index.scss';

//Data
const skillList = [ 'HTML/CSS','ES6', 'React', 'webpack', 'Babel', 'Node.js', 'Express', 'MongoDB']

const Skill = (props) => (
    <div className='skill'>
        { props.children }
    </div>
)

const Skills = (props) => {
    return (
        <div className='contentsbox-gray' id='skills'>
            <h2><span>Ski</span>lls</h2>
            <div className='skillsBox'>
                { skillList.map( (item, i) => <Skill key={item}>{item}</Skill> ) }
            </div>
        </div>
    );
}

export default Skills;