import React, { useState, useEffect, useMemo, useCallback } from 'react';
import { withStyles } from '@material-ui/core/styles';
import { Dialog, DialogContent, DialogActions, Button, useMediaQuery }from '@material-ui/core';

import ProjectData from './data';

import './index.scss';

const modalStyle = {
    paper: {
        height: '100%',
        '@media (max-width:750px)': {
            width: '100%',
            marginLeft: 5,
            marginRight: 5
        }
    },
    paperWidthSm: {
        maxWidth: 800,
    }
}

const DialogTopInfo = ({name, value}) => (
    <tr><td className='name'>{name}</td><td>{value}</td></tr>
)

const ProjectDialog = withStyles(modalStyle)(
    ({handleClose, open, projectData, classes}) => {
        const { image, title, tech, com, team, term, github, desc } = projectData;
        // console.log(useMediaQuery('(max-width:750px'))
        return (
            <Dialog classes={classes} onClose={handleClose} open={open}>
                <DialogContent divider='paper'>
                    <div className='dialog-top'>
                        <img src={image} alt='' width='384' height='216' />
                        <div className='dialog-top-info'>
                            <h3>{title}</h3>
                            <h4>{com}</h4>
                            <table>
                                <tbody>
                                    <DialogTopInfo name='기술 스택' value={tech.join(' / ')} />
                                    <DialogTopInfo name='팀 구성' value={team} />
                                    <DialogTopInfo name='기간' value={term} />
                                    <DialogTopInfo name='Github' value={<a href={github} target=':_blink'>Link</a>} />
                                </tbody>
                            </table>
                        </div>
                    </div>
                    <div className='dialog-content'>
                        {
                            desc.map( (items, i) => (
                                <React.Fragment key={`${items[0]}${i}`}>
                                    <div className='divider' />
                                    { items.map( (item, j) => typeof item === 'string' ? <p key={`${item}`}>{item}</p> : <ul key={`${item}`}>{item}</ul> ) }
                                </React.Fragment>
                            ))
                        }
                    </div>
                </DialogContent>
                <DialogActions>
                    <Button color='primary' onClick={handleClose}>Close</Button>
                </DialogActions>
            </Dialog>
        )
    }
)

const ProjectItem = (props) => {
    let [ dialogIsOpen, toggleOpen ] = useState(false);
    let {image, title, tech, com, abstract, term } = useMemo( () => props, [props] );
    let dialogOpen = useCallback( () => toggleOpen(true), [] );
    let dialogClose = useCallback( () => toggleOpen(false), [] );

    return (
        <div className='project'>
            <img className='image' src={image} alt='' width='384' height='216' />
            <div className='detail'>
                <h3>{title}</h3>
                <p className='techlist'>{tech.map( item => <span className='techshild' key={item}>{item}</span>)}</p>
                <h4>{`${com} ( ${term.split(' ').join('')} )` }</h4>
                <p>{abstract}</p>
                <p className='dialog-toggle'><Button fullWidth={true} variant="outlined" color="primary" onClick={dialogOpen}>DETAIL</Button></p>
            </div>
            { dialogOpen ? <ProjectDialog open={dialogIsOpen} handleClose={dialogClose} projectData={props} /> : null }
        </div>
    )
}

const Projects = (props) => {

    return (
        <div className='contentsbox-gray' id='projects'>
            <h2><span>Pro</span>jects</h2>
            <div className='projectlist'>
                { ProjectData.map( (item) => <ProjectItem key={item.title} {...item} />) }
            </div>
        </div>
    );
}

export default Projects;